import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

// 
let cache=localStorage.city?localStorage.city:" ";
// store
let state = {
  city:cache
};
// 
let mutations={
    changecity(state,cityname){
      this.state.city=cityname
      // console.log(this.state.city)
      localStorage.city=cityname;
    }
};
export default new Vuex.Store({
  state,//state
  mutations
})




