  .hot ul li a {
    color: #212121;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: .14rem;
  }

  //sort
  .sort h2 {
    font-size: .12rem;
    padding: .12rem .15rem;
    color: #212121;
  }

  .sort ul {
    width: 100%;
    overflow: hidden;
    background-color #fff;
  }

  .sort ul li {
    width: 16.66666%;
    height: .45rem;
    line-height: .45rem;
    font-size: .14rem;
    text-align: center;
    float: left;
    position: relative;
    z-index: 10;
    color: #212121;
  }

  /*list*/
  .list h2 {
    font-size: .12rem;
    padding: .12rem .15rem;
    color: #212121;
    background-color: #f5f5f5;
  }

  .list ul {
    width: 100%;
    overflow: hidden;
    background-color: #fff;
  }

  .list .listarra ul {
    background-color #fff;
  }

  .list .listarra {
    position relative
  }

  .list ul li {
    box-sizing: border-box;
    width: 33.3%;
    height: .45rem;
    line-height: .45rem;
    font-size: .14rem;
    text-align: center;
    border-bottom: .01rem solid #ddd;
    border-right: .01rem solid #ddd;
    margin-bottom: -0.5px;
    float: left;
    position: relative;
    z-index: 10;
    color: #212121;
  }

  .list ul li:nth-child(3n) {
    border-right: none;
  }

  .list ul li a {
    color: #212121;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: .14rem;
  }


</style>



